import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './componentes/shared/home/home.component';
import { AboutComponent } from "./componentes/shared/about/about.component";
import { HeroesComponent } from './componentes/shared/heroes/heroes.component';
import { HeroeComponent } from './componentes/shared/heroe/heroe.component';
import { BusccadorComponent } from './componentes/shared/busccador/busccador.component';



const APP_ROUTES: Routes = [  
    { path: 'home', component: HomeComponent },
    { path: 'about', component: AboutComponent },
    { path: 'heroes', component: HeroesComponent },
    { path: 'heroe/:id', component: HeroeComponent },
    { path: 'buscar/:termino', component: BusccadorComponent },
    
    { path: '**', pathMatch:'full', redirectTo: 'home' }
];

export const AppRouting = RouterModule.forRoot(APP_ROUTES,{useHash:true});

